<template>
  <div id="app">
    <div class="header_videoclip">
      <Header />
    </div>
    <div class="body_videoclip">
      <Map class="map_videoclip" :layers="layers" :sites="receivedSites" />
      <Control
        class="control_videoclip"
        @update-time="UpdateMap"
        @update-indicator="UpdateIndicator"
        @show-analytic="ShowAnalytic"
        :dates="dates"
        :wind="receivedWind"
        :tooltips="tooltips"
      />
      <Analytics
        :data="selectedDataForChart"
        :dataWind="selectedDataForChartWind"
        :dataWindDir="selectedDataForChartWindDir"
        :description="description"
        :loading="loading"
        :style="{ visibility: showAnalytic == true ? 'visible' : 'hidden' }"
      />
    </div>
    <transition name="arrow">
      <img
        class="arrow_videoclip"
        src="./assets/arrow-up.svg"
        width="74px"
        height="74px"
        v-if="show == true"
      />
    </transition>
  </div>
</template>

<script>
import Control from "./components/Control";
import Map from "./components/Map";
import Header from "./components/Header";
import Analytics from "./components/Analytics";
import { Download } from "./scripts/Download";
import { DownloadWind } from "./scripts/Download";
import { DownloadSites } from "./scripts/Download";
import { CreateLayers } from "./scripts/Download";
import { GetData } from "./scripts/Download";
import { GetDataAllTime } from "./scripts/Download";
import { CreateDates } from "./scripts/Download";
import { CreateColoredTooltips } from "./scripts/Download";
import { CreatePDK } from "./scripts/Download";
import { DirectionToText } from "./scripts/Download";
import { DirectionRound } from "./scripts/Download";
import { AVGperHour } from "./scripts/Download";
import { CreateDataForChart } from "./scripts/Download";
export default {
  name: "App",
  data() {
    return {
      receivedData: [],
      receivedDataHour: [],
      receivedWind: [],
      receivedSites: [],
      selectedDataForChart: [],
      selectedDataForChartWind: [],
      selectedDataForChartWindDir: [],
      savedDataforChart: [],
      savedDataforChartWind: [],
      layers: [],
      tooltips: [],
      dates: [],
      indicator: "PM",
      description: "    /3",
      time: "",
      show: true,
      loading: false,
      showAnalytic: false,
    };
  },
  components: {
    Map,
    Control,
    Header,
    Analytics,
  },
  mounted() {
    CreateDates().then((dates) => {
      this.time = dates[0];
      DownloadSites(dates).then((data) => {
        this.receivedSites = data;
      });
      Download(dates, ["PM", "AQI", "TEMP"], "1day").then((data) => {
        /*   */
        const PM = GetDataAllTime(data, "PM");
        const PDK = CreatePDK(PM);
        data.push(...PDK);
        /*  */
        const rememberIndicator = localStorage.getItem(
          "KNC_VIDEOCLIP_INDICATOR"
        );
        if (rememberIndicator) {
          this.show = false;
          this.indicator = rememberIndicator;
        }
        this.dates = dates;
        this.receivedData = data;

        this.tooltips = CreateColoredTooltips(data, dates, this.indicator);
        const dataForLayers = GetData(data, dates[0], this.indicator);
        this.layers = CreateLayers(dataForLayers, this.indicator);
      });
      DownloadWind(dates, ["SPEED", "DIR"], "1day").then((data) => {
        const windDir = GetDataAllTime(data, "DIR").map((d) => {
          return DirectionToText(d.value);
        });
        const windSpeed = GetDataAllTime(data, "SPEED").map((d) => {
          const v = Number(d.value);
          return v.toFixed(0);
        });
        this.receivedWind = {
          windDir,
          windSpeed,
        };
      });
    });
  },
  methods: {
    /*    Loadind(state) {}, */
    ShowAnalytic() {
      this.showAnalytic = !this.showAnalytic;
      this.UpdateAnalytic(false);
    },
    UpdateAnalytic(play) {
      if (this.showAnalytic && play == false) {
        const datesForAnalytic = [this.time, this.time];
        const data = GetData(this.savedDataforChart, this.time, this.indicator);
        if (data.length != 0) {
          this.selectedDataForChart = data[0].datasets;
        } else {
          if (this.indicator != "PDK") {
            this.loading = true;
            Download(datesForAnalytic, [this.indicator], "1hour").then(
              (data) => {
                const indicatorData = GetDataAllTime(data, this.indicator);
                const avg = AVGperHour(indicatorData, this.indicator);
                const datasets = CreateDataForChart(avg, this.indicator);
                this.savedDataforChart.push({
                  time: this.time,
                  indicator: this.indicator,
                  datasets: datasets,
                });
                this.selectedDataForChart = datasets;
                this.loading = false;
              }
            );
          } else {
            this.loading = true;
            Download(datesForAnalytic, ["PM"], "1hour").then((data) => {
              const indicatorData = GetDataAllTime(data, "PM");
              const PDK = CreatePDK(indicatorData);
              const avg = AVGperHour(PDK, this.indicator);
              const datasets = CreateDataForChart(avg, this.indicator);
              this.savedDataforChart.push({
                time: this.time,
                indicator: this.indicator,
                datasets: datasets,
              });
              this.selectedDataForChart = datasets;
              this.loading = false;
            });
          }
        }
        
        const dataWind = GetData(this.savedDataforChart, this.time, "SPEED");
        if (dataWind.length != 0) {
          this.selectedDataForChartWind = dataWind[0].datasets;
        } else {
          DownloadWind(datesForAnalytic, ["SPEED"], "1hour").then((data) => {
            const windSpeed = GetDataAllTime(data, "SPEED").map((d) => {
              const v = Number(d.value);
              return { hour: d.hour, value: v.toFixed(0) };
            });
            const datasets = CreateDataForChart(windSpeed, "SPEED");
            this.savedDataforChart.push({
              time: this.time,
              indicator: "SPEED",
              datasets: datasets,
            });
            this.selectedDataForChartWind = datasets;
          });
        }

        const dataWindDir = GetData(this.savedDataforChart, this.time, "DIR");
        if (dataWindDir.length != 0) {
          this.selectedDataForChartWindDir = dataWindDir[0].datasets;
        } else {
          DownloadWind(datesForAnalytic, ["DIR"], "3hour").then((data) => {
            const windDir = GetDataAllTime(data, "DIR").map((d) => {
              return {
                hour: d.hour,
                value: DirectionRound(d.value),
                text: DirectionToText(d.value),
              };
            });
            this.savedDataforChart.push({
              time: this.time,
              indicator: "DIR",
              datasets: windDir,
            });
            this.selectedDataForChartWindDir = windDir;
          });
        }
      }
    },
    UpdateMap(index, play) {
      this.time = index;
      const timeData = GetData(this.receivedData, this.time, this.indicator);
      this.layers = CreateLayers(timeData, this.indicator);

      this.UpdateAnalytic(play);
    },
    UpdateIndicator(type, description) {
      this.show = false;
      this.indicator = type;
      this.description = description;
      const timeData = GetData(this.receivedData, this.time, this.indicator);
      this.layers = CreateLayers(timeData, type);
      this.tooltips = CreateColoredTooltips(
        this.receivedData,
        this.dates,
        type
      );

      this.UpdateAnalytic(false);
    },
  },
};
</script>

